.sobre-mim {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  gap: 2rem;

  .foto-container {
    position: relative;

    .imagem-wrapper {
      position: relative;
      z-index: 1;
      border-radius: 10px;
      overflow: hidden;
      background-color: #2c2f34;
      padding: 1rem;

      .foto-perfil {
        width: 220px;
        max-width: 100%;
        height: auto;
        border-radius: 10px;
        display: block;
      }
    }
  }

  .descricao {
    background-color: #f1f1f1;
    padding: 1.5rem;
    border-radius: 10px;
    max-width: 700px;
    color: #1c1e22;

    p {
      margin-bottom: 1rem;
      font-size: 1rem;
      line-height: 1.5;
    }

    .botao-curriculo {
      background-color: #ff6600;
      color: white;
      border: none;
      padding: 0.6rem 1.5rem;
      font-weight: bold;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s ease;

      &:hover {
        background-color: #e05500;
      }
    }
  }

  // Responsividade
  @media (max-width: 900px) {
    flex-direction: column;
    padding: 1rem;
    gap: 1.5rem;

    .foto-container {
      .imagem-wrapper {
        padding: 0.5rem;

        .foto-perfil {
          width: 180px;
        }
      }
    }

    .descricao {
      max-width: 90%;
      text-align: start; 
      p {
        font-size: 0.95rem;
      }

      .botao-curriculo {
        padding: 0.5rem 1.2rem;
        font-size: 0.95rem;
      }
    }
  }

  @media (max-width: 480px) {
    padding: 0.5rem;
    gap: 1rem;

    .foto-container {
      .imagem-wrapper {
        padding: 0.3rem;
        .foto-perfil {
          width: 140px;  // reduz ainda mais a imagem em mobile pequeno
        }
      }
    }

    .descricao {
      max-width: 100%;
      p {
        font-size: 0.85rem;
      }

      .botao-curriculo {
        width: 100%;
        padding: 0.6rem 0;
        font-size: 0.9rem;
      }
    }
  }
}
